<script lang="ts">
    import Alert from "./Alert.svelte";
    import {
        InputGroup,
        InputGroupText,
        Input,
        Button,
        Container,
    } from "sveltestrap";
    import { onMount } from "svelte";
    import axios from "axios";
    import qs from "qs";
    import { serverUrl, uid } from "./store";

    let serverMsg = "";
    let userid, nickname, age, gender;
    onMount(async () => {
        let info = (
            await axios.get(
                $serverUrl + "/api/user/info?" + qs.stringify({ uid: $uid }),
                {
                    withCredentials: true,
                }
            )
        ).data;
        console.log(info);
        userid = info.userid;
        nickname = info.nickname;
        age = info.age;
        gender = info.gender;
    });

    async function handleSubmit() {
        const ids = ["userid", "cur_pw", "new_pw", "nickname", "age"];
        let querys = ids.reduce((res, key) => {
            res[key] = document.getElementById(key).value;
            return res;
        }, {});
        querys["uid"] = $uid;
        const res = await axios.get(
            $serverUrl + "/api/user/update?" + qs.stringify(querys),
            { withCredentials: true }
        );
        if (res.data.success) serverMsg = "  !";
        else serverMsg = res.data.message;
    }
</script>

<div>
    <Container>
        <h2> </h2>
        <br />
        <InputGroup>
            <InputGroupText>ID</InputGroupText>
            <Input id="userid" value={userid} readonly />
        </InputGroup>
        <br />
        <InputGroup>
            <InputGroupText> </InputGroupText>
            <Input id="cur_pw" type="password" placeholder="current password" />
        </InputGroup>
        <br />
        <InputGroup>
            <InputGroupText> </InputGroupText>
            <Input id="new_pw" type="password" placeholder="new password" />
        </InputGroup>
        <br />
        <InputGroup>
            <InputGroupText></InputGroupText>
            <Input id="nickname" value={nickname} placeholder="nickname" />
        </InputGroup>
        <br />
        <InputGroup>
            <InputGroupText></InputGroupText>
            <Input id="age" value={age} placeholder="age" />
        </InputGroup>
        <br />
        <InputGroup>
            <InputGroupText></InputGroupText>
            <Input id="gender" value={gender} readonly />
        </InputGroup>
        <br />
        <Button on:click={handleSubmit}></Button>
    </Container>
    {#if serverMsg != ""}
        <Alert bind:serverMsg />
    {/if}
</div>
